<template>
	<div class="wrap" v-loading="isLoading">
		<div class="register">
			<div class="form">
				<ul>
					<li><input type="text" v-model="formData.name" placeholder="请输入用户名"></li>
					<li><input type="text" v-model="formData.contacts" placeholder="请输入联系人"></li>
					<li><input type="text" v-model="formData.phone" placeholder="请输入电话"></li>
					<li><input type="text" v-model="formData.address" placeholder="请输入地址"></li>
					<li><input type="text" v-model="formData.account" placeholder="请输入账号"></li>
					<li><input type="password" v-model="formData.password" placeholder="请输入密码"></li>
				</ul>
			</div>
			<div class="buttons">
				<a href="javascript:;" @click="Submit">提交</a>
				<!-- <a href="javascript:;" v-route-to="`/login`" class="small">返回登录</a> -->
			</div>
		</div>
	</div>
</template>

<script>
import Cookie from 'vue-cookie'
export default {
	methods: {
		Submit () {
			if (!this.formData.name) { this.$message({ type: 'error', message: '用户名不能为空!' }); return false; }
			if (!this.formData.contacts) { this.$message({ type: 'error', message: '联系人不能为空!' }); return false; }
			if (!this.formData.phone) { this.$message({ type: 'error', message: '电话不能为空!' }); return false; }
			if (!this.formData.address) { this.$message({ type: 'error', message: '地址不能为空!' }); return false; }
			if (!this.formData.account) { this.$message({ type: 'error', message: '账号不能为空!' }); return false; }
			if (!this.formData.password) { this.$message({ type: 'error', message: '密码不能为空!' }); return false; }
			this.isLoading = true
            // this.axios.post('/sys/saveUser', this.formData,
            this.axios.post('/software/saveUser', this.formData,
				// { headers: { "Content-Type": "application/x-www-form-urlencoded" } }
			).then(rs => {
				this.isLoading = false
				if (rs.data.code == 0) {
					// this.$router.push('/login')

					Cookie.set('UserInfo', JSON.stringify({ account: this.formData.account }))
					Cookie.set('token', true)
					
					this.$emit('close', true)
					this.$message({ type: 'success', message: '注册成功' });
				} else {
					this.$message({ type: 'error', message: rs.data.msg });
				}
			})

			// this.axios('/mould/selectList', this.formData).then(rs => {
			// 	console.log(rs.data)
			// })
		}
	},
	data () {
		return {
			isLoading: false,
			formData: {
				// name: "",  account: "", password: "", contacts: "", phone: "", address: ""
				"name":"昵称1", 
				"account":"16728493041",
				"password":"123456",  
				"contacts":"联系人2",  
				"phone":"电话2",
				"address":"地址2"
			}
		}
	}
}
</script>
<style lang="less" scoped>
@import url('./Register.less');
</style>