<template>
    <div class="login">
        <div class="wrap">
            <div class="header">
                <img src="../../assets/logo.png" class="logo">
            </div>
            <div class="content">
                <div class="form">
                    <p class="text-right"><a href="javascript:;" class="f24">下载到桌面</a></p>
                    <p class="text-right"><a href="javascript:;" class="f18">确认</a></p>
                    <ul>
                        <li>账号：<input type="text" v-model="formData.account" placeholder="请输入账号"></li>
                        <li>密码：<input type="password" v-model="formData.password" placeholder="请输入账号"></li>
                        <li class="links">
                            <a href="javascript:;" @click="SetDefault">默认账号</a>
                            <a href="javascript:;" @click="Submit">登录</a>
                            <!-- <a href="javascript:;" v-route-to="`/register`">免费注册</a> -->
                            <!-- <a href="javascript:;" @click="isDialogRegisterOpen = true">免费注册</a> -->
                        </li>
                    </ul>
                </div>
            </div>
            <div class="footer">
                <p>广东省汕头市澄海区白云软件开发有限公司</p>
                <p>粤ICP备16007973号</p>
            </div>
        </div>

        <DialogRegister :open="isDialogRegisterOpen" @close="isDialogRegisterOpen = false" />

        <i class="icon-star star star-1"></i>
        <i class="icon-star star star-2"></i>
        <i class="icon-star star star-3"></i>

    </div>
</template>

<script>
import Cookie from 'vue-cookie'

import DialogRegister from '@src/components/dialog.register/dialog.register'

export default {
    components: {
        DialogRegister
    },
    methods: {
        SetDefault () {
            this.formData = Object.assign(this.formData, { account: 'admin', password: '123456' })
            //   this.$message({ type: 'success', message: '设置默认账号' })
        },
        Submit () {
            if (!this.formData.account) { this.$message({ type: 'error', message: '账号不能为空!' }); return false; }
            if (!this.formData.password) { this.$message({ type: 'error', message: '密码不能为空!' }); return false; }
            this.axios.post('/anno/login', this.formData,
                { headers: { "Content-Type": "application/x-www-form-urlencoded" } }
            ).then(rs => {
                if (rs.data.code == 0) {
                    Cookie.set('UserInfo', JSON.stringify({ account: this.formData.account }))
                    Cookie.set('token', true)
                    this.$message({ type: 'success', message: '登录成功' });
                } else {
                    this.$message({ type: 'error', message: rs.data.msg });
                }
            })
        }
    },
    mounted () {
    },
    data () {
        return {
            isDialogRegisterOpen: false,
            formData: { account: '', password: '' }
        }
    }
}
</script>
<style lang="less" scoped>
@import url('./Login.less');
</style>
